import { doc } from "firebase/firestore";

const btn = document.querySelector('.support-ukraine__btn');
const list = document.querySelector('.support-ukraine__list');
const container = document.querySelector('.support-ukraine');

const onFondsBtnClick = () => {
    list.classList.toggle('open');
    container.classList.toggle('open');
    btn.classList.toggle('open');
}

btn.addEventListener('click', onFondsBtnClick);